.gallery {
    height: 136px;
    background-image: linear-gradient(to bottom, getColor(background-section) 20%,
            getColor(active-white) 20%);
    display: flex;
    justify-content: center;

    @include bpoint(tablet) {
        height: 325px;
    }

    @include bpoint(desktop) {
        height: 430px;

    }
}

.gallery__list {
    position: relative;
    @extend %list;
    width: 320px;

    @include bpoint(tablet) {
        width: 747px;
    }

    @include bpoint(desktop) {
        width: 1000px;
    }
}

.gallery__item {
    overflow: hidden;

    &:nth-child(1) {
        position: absolute;
        left: 0;
        top: 46px;
    }

    &:nth-child(2) {
        position: absolute;
        left: 46px;
        top: 24px;
    }

    &:nth-child(3) {
        position: absolute;
        left: 46px;
        top: 70px;
    }

    &:nth-child(4) {
        position: absolute;
        left: 92px;
        top: 0;
    }

    &:nth-child(5) {
        position: absolute;
        left: 92px;
        top: 92px;
    }

    &:nth-child(6) {
        position: absolute;
        left: 184px;
        top: 30px;
    }

    &:nth-child(7) {
        position: absolute;
        left: 184px;
        top: 76px;
    }

    &:nth-child(8) {
        position: absolute;
        left: 230px;
        top: 15px;
    }

    &:nth-child(9) {
        position: absolute;
        left: 230px;
        top: 61px;
    }

    &:nth-child(10) {
        position: absolute;
        left: 276px;
        top: 40px;
    }

    &:hover .gallery__img,
    &:focus .gallery__img {
        transform: scale(1.2);



    }

    @include bpoint(tablet) {
        &:nth-child(1) {
            position: absolute;
            left: 0;
            top: 110px;
        }

        &:nth-child(2) {
            position: absolute;
            left: 107px;
            top: 55px;
        }

        &:nth-child(3) {
            position: absolute;
            left: 107px;
            top: 165px;
        }

        &:nth-child(4) {
            position: absolute;
            left: 214px;
            top: 0;
        }

        &:nth-child(5) {
            position: absolute;
            left: 214px;
            top: 220px;
        }

        &:nth-child(6) {
            position: absolute;
            left: 428px;
            top: 65px;
        }

        &:nth-child(7) {
            position: absolute;
            left: 428px;
            top: 175px;
        }

        &:nth-child(8) {
            position: absolute;
            left: 535px;
            top: 45px;
        }

        &:nth-child(9) {
            position: absolute;
            left: 535px;
            top: 155px;
        }

        &:nth-child(10) {
            position: absolute;
            left: 642px;
            top: 120px;
        }
    }

    @include bpoint(desktop) {
        &:nth-child(1) {
            position: absolute;
            left: 0;
            top: 148px;
        }

        &:nth-child(2) {
            position: absolute;
            left: 143px;
            top: 75px;
        }

        &:nth-child(3) {
            position: absolute;
            left: 143px;
